.popup__close-icon {
  width: 32px;
  height: 32px;
  color: #fff;
  transition: opacity .5s;
}

.popup__close-icon:hover {
  opacity: 0.6;
  cursor: pointer;
  transition: opacity .5s;
}

@media screen and (max-width: 600px) {
  .popup__close-icon {
    width: 20px;
    height: 20px;
  }
}